@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&family=Saira:ital,wght@0,100..900;1,100..900&display=swap";
body {
  margin: 0;
  overflow: hidden;
}

#nav-controls {
  gap: 10px;
  display: flex;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

#nav-controls button {
  color: #fff;
  cursor: pointer;
  background: #0009;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 24px;
}

#nav-controls button:hover {
  background: #000000e6;
}

#tooltip {
  color: #f5f5f5;
  pointer-events: none;
  background: #1e1e1ee6;
  border-radius: 10px;
  max-width: 220px;
  padding: 12px 14px;
  font-family: montserrat;
  font-size: 14px;
  line-height: 1.4;
  display: none;
  position: absolute;
  box-shadow: 0 4px 12px #0006;
}
